.footer {
    padding: 2rem 6rem;
    background-color: var(--htmlBlack);
    color: #fff;

}

.top{
    display: flex;
    align-items: center;
    text-align: start;
    justify-content: space-between;
    flex-wrap: wrap;
}

.top i{
    color: #fff;
    font-size: 2rem;
    margin-left: 1rem;
}
.top i:hover{
    color: #01959a;
}

.bottom{
    text-align: start;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 4rem;
}

.bottom div{
    display: flex;
    flex-direction: column;
}

.bottom h4{
    font-size: 1.3rem;
    padding:  1rem 0 .8rem 0;
}

.bottom a{
    color: #fafafa;
    font-size: 1.1rem;
    padding-bottom: 0.4rem;
    text-decoration: none;
}

@media screen and (max-width : 850px){
    .footer{
        padding: 4rem 2rem;
    }
    .footer i {
        margin: 1rem 1rem 0 0;
    }
    .bottom div{
        width: 50%;
    }
}